section {
    display: flex;
    font-size: 1.4rem;
    padding: 0.25rem;
    justify-content: center;
}

.balance_btn {
    display: inline-block;
    box-sizing: border-box;
    padding: 0 20px;
    outline: none;
    border: none;
    border-radius: 6px;
    height: auto;
    line-height: 30px;
    font-size: 14px;
    font-weight: 600;
    text-decoration: none;
    color: #385898;
    background-color: #e7f3ff;
    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;
    appearance: none;
    touch-action: manipulation;
}

.balance_btn:focus-visible {
	box-shadow: 0 0 0 2px #666;
}
.balance_btn:hover {
	background-color: #DBE7F2;
}
.balance_btn:active {
	transform: scale(0.96);
}
.btn:disabled {
	pointer-events: none;
	opacity: 0.65;
}

.balance_text{
    margin-right: 5em;
}