.coin-table {
	border-collapse: collapse; 
}

.coin-table tr:hover{
	background-color: #DBE7F2;
    cursor: pointer;
}

.coin-table th {
	border-bottom: 1px solid #dddddd;
}
.coin-table td {
    width: 90vh;
    border-bottom: 1px solid #dddddd;
}
.coin-table .th-letf {
    text-align: left;
}

.coin-table .text-left  {
    text-align: left;
    font-weight: 500;
}

.coin-table .text-right  {
    text-align: right;
    font-weight: 500;
}

.btn_buy{
    display: inline-block;
    box-sizing: border-box;
    padding: 0 8px;
    outline: none;
    border: none;
    border-radius: 6px;
    height: auto;
    line-height: 25px;
    font-size: 13px;
    font-weight: 500;
    cursor: pointer;
    background: linear-gradient(#26C778 0%, #32CA76 67%, #5DD36D 100%);
    color: #FFF;
}

.btn_sell{
    display: inline-block;
    box-sizing: border-box;
    padding: 0 8px;
    outline: none;
    border: none;
    border-radius: 6px;
    height: auto;
    line-height: 25px;
    font-size: 13px;
    font-weight: 500;
    cursor: pointer;
    background: linear-gradient(#FF3038 0%, #FF3829 67%, #FF4314 100%);
    color: #FFF;
}
.btn_refresh {
    display: inline-block;
    box-sizing: border-box;
    padding: 0 20px;
    outline: none;
    border: none;
    border-radius: 6px;
    height: auto;
    line-height: 30px;
    font-size: 14px;
    font-weight: 600;
    text-decoration: none;
    color: #385898;
    background-color: #e7f3ff;
    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;
    appearance: none;
    touch-action: manipulation;
}

.btn_refresh:focus-visible {
	box-shadow: 0 0 0 2px #666;
}
.btn_refresh:hover {
	background-color: #DBE7F2;
}
.btn_refresh:active {
	transform: scale(0.96);
}

.logo_refresh{
    height: 0.8rem;
}

.minus_price{
    color: #ea3943;
}

.plus_price{
    color: #16c784;
}

.icon_plus::before{
    display: inline-block;
    content: '';
    border: 4px solid transparent;
    border-bottom: 6px solid #16c784;
    margin: 2px;
}

.icon_minus::before{
    display: inline-block;
    content: '';
    border: 4px solid transparent;
    border-bottom: 6px solid #ea3943;
    margin: 2px;
}